body {
    position: relative;
}

.header {
    display: inline;
}


.trees_i {
    width: 100%;
    height: 300px;
    object-fit: cover;
}

.navbar_container {
    position: relative;
    display: inline;
    bottom: 70px;
}

#navbar {
    background: linear-gradient(0.1turn, #ddda, #ffff);
    align-self: flex-start;
    padding-left: 100px;
    position: sticky;
    overflow: visible;
    display: flex;
    max-height: 70px;
    top: 0;
}

.nav_link {
    align-self: flex-start;
    padding: 10px 20px;
    margin: 10px 20px;
    top: 15px;
    vertical-align: center;

    font-family:  "Trebuchet MS", sans-serif;
    font-size: large;
    border-style: solid;
    border-width: medium;
    border-radius: 5px;
    color: #555;
}